body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
    text-align: center;
    /*height: max(100vh, 100%);*/
    min-height: 100vh;
    display: flex;
    flex-flow: column nowrap;
    background-color: black;
    color: white;
}


/* Probably can remove the unused classes below */

.App-header {
    /*min-height: 100vh;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    /*justify-content: center;  this is for vertical alignment*/
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}


@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.content {
    flex-grow: 1;
}

.header {
    background-color: black;
    /*background-image: url("../assets/images/fire_header_wide.jpg");*/
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    /*height: 10%;*/
    color: white;

    max-width: 100vw;
}

.header-links {
    flex-direction: row;

    bottom: 0;
    position: relative;
}

.header-nav-button {
    align-content: start;
    padding: 10px;
    margin: 10px;

    color: white;
    background-color: grey;

    border-radius: 2px;
}

.header-nav-button-selected {
    align-content: start;
    padding: 10px;
    margin: 10px;

    color: white;
    background-color: grey;

    border-radius: 2px;
    border-width: 2px;
    border-color: white;
    border-style: solid;
}

.header-image {
    max-width: 100%;
    object-fit: contain;
}

.footer {
    bottom: 0;
    position: relative;
    color: white;
}

.image-carousel-container {
    justify-content: center;

    padding: 5%;
}

.image-carousel {
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.base-page {
    margin: 5%;
}

.experience {
    margin-bottom: 5%;
}
