.app {
    text-align: center;
    /*height: max(100vh, 100%);*/
    min-height: 100vh;
    display: flex;
    flex-flow: column nowrap;
    background-color: black;
    color: white;
}


/* Probably can remove the unused classes below */

.App-header {
    /*min-height: 100vh;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    /*justify-content: center;  this is for vertical alignment*/
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}


@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.content {
    flex-grow: 1;
}
